<template>
    <div class="data-gerai">
        <h3 class="mb-10">Data Gerai</h3>
        <vx-card>
            <vs-table
              :data="gerai"
               pagination search>
                <template slot="header">
                    <vx-tooltip text="Tambah Gerai" style="display:inline">   
                        <vs-button @click="addGerai = true" class="float-right mb-3" color="#0fabbc" icon="add_circle_outline"> Gerai</vs-button>
                    </vx-tooltip>
                </template>
                <template slot="thead" >
                    <vs-th sort-key="nama_gerai" class="bg-primary text-white text-center">
                    Nama
                    </vs-th>
                    <vs-th sort-key="region" class="bg-primary text-white text-center">
                    Region
                    </vs-th>
                    <vs-th sort-key="wilayah" class="bg-primary text-white text-center">
                    Wilayah 
                    </vs-th>
                    <vs-th sort-key="cabang" class="bg-primary text-white text-center" >
                    Cabang
                    </vs-th>
                    <vs-th sort-key="area" class="bg-primary text-white text-center" >
                    Area
                    </vs-th>
                    <vs-th class="bg-primary text-white text-center">
                    Aksi
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :data="item" :key="index" v-for="(item, index) in data" >
                    <vs-td class="text-center" style="width:10%;">{{item.tglRequest}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.tglKirim}}</vs-td>
                    <vs-td style="width:55%;">{{item.isi}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.countNumber}}</vs-td>
                    <vs-td class="text-center" style="width:5%;"><vs-button color="#0fabbc" size="small" icon="pageview" ></vs-button></vs-td>
                    </vs-tr>
                </template>
            </vs-table>

            <!-- Tambah Gerai -->
            <vs-popup class="holamundo"  title="Tambah Gerai" :active.sync="addGerai">
                <Create />
            </vs-popup>
        </vx-card>
    </div>
</template>

<script>
import Create from './create.vue'
import Edit from './edit.vue'

export default {
    name: 'data-gerai',
    data() {
        return {
            gerai: [],
            addGerai: false
        }
    },
    components: {Create,Edit}
}
</script>